import path from "path";
import { build } from "esbuild";
import { green } from "picocolors";
import { scanPlugin } from "./scanPlguin";
import { preBundlePlugin } from "./preBundlePlugin";
import { PRE_BUNDLE_DIR } from "../constants";
//‰æùËµñÈ¢ÑÊûÑÂª∫--Á¨¨‰∏âÊñπ‰æùËµñËøõË°åÊâìÂåÖÔºåÂπ∂Âú®ÂºÄÂèëÁéØÂ¢É‰∏ã‰ΩøÁî®Ëøô‰∫õÊâìÂåÖËøáÁöÑÁ¨¨‰∏âÊñπ‰æùËµñ„ÄÇ
export async function optimize(root: string) {
  // 1. Á°ÆÂÆöÂÖ•Âè£
  const entry = path.resolve(root, "src/main.tsx");
  // 2. ‰ªéÂÖ•Âè£Â§ÑÊâ´Êèè‰æùËµñ
  const deps = new Set<string>();
  //deps ÊòØ‰∏Ä‰∏™ Set ÈõÜÂêàÔºåÁî®‰∫éÂ≠òÂÇ®Êâ´ÊèèÂà∞ÁöÑ‰æùËµñÈ°π„ÄÇ
  await build({
    entryPoints: [entry],
    //ÊåáÂÆö‰∫ÜÂÖ•Âè£Êñá‰ª∂ÁöÑË∑ØÂæÑ
    bundle: true,
    //ËøõË°åÊâìÂåÖ„ÄÇ
    write: false,
    //‰∏çÂ∞ÜÊûÑÂª∫ÁªìÊûúÂÜôÂÖ•Êñá‰ª∂ÔºåËÄåÊòØÂ∞ÜÂÖ∂‰øùÁïôÂú®ÂÜÖÂ≠ò‰∏≠
    plugins: [scanPlugin(deps)],
    //ÂåÖÂê´Êâ´Êèè‰æùËµñÈÄªËæëÁöÑÊèí‰ª∂Êï∞ÁªÑ
  });
  console.log(
    `${green("ÈúÄË¶ÅÈ¢ÑÊûÑÂª∫ÁöÑ‰æùËµñ")}:\n${[...deps]
      .map(green)
      .map((item) => `  ${item}`)
      .join("\n")}`
  );
  // 3. È¢ÑÊûÑÂª∫‰æùËµñ
  await build({
    entryPoints: [...deps],
    write: true,
    bundle: true,
    format: "esm",
    splitting: true,
    outdir: path.resolve(root, PRE_BUNDLE_DIR),
    plugins: [preBundlePlugin(deps)],
  });
}
