import { Plugin } from "esbuild";
import { BARE_IMPORT_RE, EXTERNAL_TYPES } from "../constants";
export function scanPlugin(deps: Set<string>): Plugin {
  return {
    name: "esbuild:scan-deps",
    setup(build) {
      // ÂøΩÁï•ÁöÑÊñá‰ª∂Á±ªÂûã
      build.onResolve(
        //ÂåπÈÖçÁî®‰∫éËøáÊª§ÈúÄË¶ÅË¢´Â§ÑÁêÜÁöÑ‰æùËµñÈ°π
        { filter: new RegExp(`\\.(${EXTERNAL_TYPES.join("|")})$`) },
        (resolveInfo) => {
          return {
            path: resolveInfo.path,
            // Êâì‰∏ä external Ê†áËÆ∞
            external: true,
            //‰∏çÈúÄË¶ÅÂ§ÑÁêÜÁöÑÂ∞±Êâì‰∏äÊ†áÁ≠æ
          };
        }
      );
      // ËÆ∞ÂΩï‰æùËµñ
      build.onResolve(
        {
          filter: BARE_IMPORT_RE,
        },
        (resolveInfo) => {
          const { path: id } = resolveInfo;
          // Êé®ÂÖ• deps ÈõÜÂêà‰∏≠
          // idÂèòÈáèÂºïÁî®‰∫ÜresolveInfo.path
          deps.add(id);
          return {
            path: id,
            external: true,
          };
        }
      );
    },
  };
}
