import os from "os";
import path from "path";
export function slash(p: string): string {
  return p.replace(/\\/g, "/");
  //Â∞ÜÁªôÂÆöË∑ØÂæÑ‰∏≠ÁöÑÂèçÊñúÊù† \ ÊõøÊç¢‰∏∫ÊñúÊù† /ÔºåÂÆûÁé∞Ë∑ØÂæÑÁöÑÊ†áÂáÜÂåñ„ÄÇ
}

export const isWindows = os.platform() === "win32";
//Âà§Êñ≠ÂΩìÂâçÊìç‰ΩúÁ≥ªÁªüÊòØÂê¶‰∏∫ Windows
export function normalizePath(id: string): string {
  //path.posix.normalize() ÊñπÊ≥ïÂØπË∑ØÂæÑËøõË°åËßÑËåÉÂåñÂ§ÑÁêÜ
  return path.posix.normalize(isWindows ? slash(id) : id);
}
